
.notifications .notifications-div {
    /* background: #ffffff; */
    box-shadow:  0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin-top: 3%;
    padding: 8px 8px;
}
.notifications .notifications-div .all-cnt{
    overflow-y: scroll; 
    height: 720px;
}
.notifications .notifications-div .all-cnt::-webkit-scrollbar {
    display: none;
}
.notifications .single-notification-result{
    padding: 6px 8px;
    border-bottom: 1px solid rgb(184, 211, 184);
}

.notifications .single-notification-result:hover{
    background: white;
    border-radius: 10px;
    transition: all;
}

.notifications .single-notification-green{
    background: rgb(224, 238, 224);
}

.notifications .single-notification-red{
    background: rgb(247, 235, 235);
}

.notifications .single-notification-result .notification-link{
    text-decoration: none;
}

.notifications  .single-notification-result .notification-title{
    color: #65676b;
    line-height: 18px;
    padding-top: 2px;
}

.notifications  .single-notification-result .notification-time{
    color: #1976f2;
    font-weight: 800;
    padding-top: 3px;
}

